input[type=text] {
  width: 100%;
  padding: .75rem;
  border: .0625rem solid #ccc;
  border-radius: .25rem;
  resize: vertical;
}

label {
  padding: .75rem .75rem .75rem 0;
  display: inline-block;
}

button {
  background-color: #04AA6D;
  color: white;
  padding: .75rem 1.25rem;
  border: none;
  border-radius: .25rem;
  cursor: pointer;
  float: right;
}

button:hover {
  background-color: #45a049;
}

.container {
  border-radius: .3125rem;
  background-color: #b1b0b0;
  padding: 2rem;
  margin: 2rem;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}